.Multibox{
  position: absolute;
  width: 80vh;
  height: 50vh;
  float: right;
  right: 30%;
  top: 113vh;
  box-shadow: 0px 0px 15px rgb(82, 82, 82);
  background-color: snow;
  
}

.m111{
  position: relative;
  top: 40px;
  color: rgb(116, 116, 116);
  text-align: center;
}











  .latest {
    width: 220px;
    height: 50px;
    bottom: 4vh;
    right: 5vh;
    border: none;
    outline: none;
    color: snow;
    background: #111;
    cursor: pointer;
    position: absolute;
    z-index: 0;
    border-radius: 10px;
    font-size: 20px;
  }
  
  .latest:before {
    content: '';
    background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
    position: absolute;
    top: -2px;
    left:-2px;
    background-size: 400%;
    z-index: -1;
    filter: blur(5px);
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    animation: glowing 20s linear infinite;
    opacity: 0;
    transition: opacity .3s ease-in-out;
    border-radius: 10px;
  }
  
  .latest:active {
    color: #000
  }
  
  .latest:active:after {
    background: transparent;
  }
  
  .latest:hover:before {
    opacity: 1;
  }
  
  .latest:after {
    z-index: -1;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: #111;
    left: 0;
    top: 0;
    border-radius: 10px;
  }
  
  @keyframes glowing {
    0% { background-position: 0 0; }
    50% { background-position: 400% 0; }
    100% { background-position: 0 0; }
  }




















  /* ------------------------------------------------------------------------------------------------------------------- */



  @media only screen and (max-width: 600px) {
    .Multibox{
      position: relative;
      width: 90%;
      height: 50vh;
      color: white;
      box-shadow: 0px 0px 15px rgb(82, 82, 82);
      border-radius: 10px;
      left: -5%;
      top: 5vh;
    }
  
    .m111{
      position: relative;
      margin: auto;
      color: rgb(116, 116, 116);
      left: 0px;
      font-family: quat;
      font-size: 40px;
      align-self: center; 
      top: 20px;
    }
  }
  