}

.skillab:hover{
    text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #03ffffb6, 0 0 20px #03ffffb6, 0 0 25px #03ffffb6, 0 0 30px #03ffffb6, 0 0 35px #03ffffb6;
    transition: .2s;
    color: #2f2f2f;
}


.box1{
    position: relative;
    width: 40vh;
    height: 60vh;
    border-radius: 10px;
    background-color: rgb(90, 90, 90);
    left: 5vh;
    top: 15vh;
    background-color: #2f2f2f;
    background-image: linear-gradient(157deg, #2f2f2f 0%, #b9b9b9 100%);
}

.box1:hover{
