  }

  .dis1{
    position: absolute;
    top: 28vh;
    left: 170px;
    font-family: portfolio;
    font-weight: normal;
    color: rgb(61, 61, 61);
    font-size: 2vh;
  }

  .container {
    width: 400px;
    height: 400px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .center {
    width: 180px;
    height: 60px;
    position: absolute;
  }
  
  .btn {
    position: absolute;
    top: 40px;
    width: 180px;
    height: 60px;
    cursor: pointer;
    background: transparent;
    border: 1px solid #000000;
    outline: none;
    transition: 1s ease-in-out;
  }
  
  svg {
    position: absolute;
    left: 0;
    top: 0;
    fill: none;
    stroke: rgb(0, 0, 0);
    stroke-dasharray: 150 480;
    stroke-dashoffset: 150;
    transition: 1s ease-in-out;
  }
  
  .btn:hover {
    transition: 1s ease-in-out;
    background: #5e5e5e;
  }
  
  .btn:hover svg {
    stroke-dashoffset: -480;
  }
  
  .btn span {
    color: rgb(0, 0, 0);
    font-size: 18px;
    font-weight: 100;
  }

  .btn2 {
    position: absolute;
    top: 130px;
    width: 180px;
    height: 60px;
    cursor: pointer;
    background: transparent;
    border: 1px solid #ffffff;
    outline: none;
    transition: 1s ease-in-out;
  }
  
  svg {
    position: absolute;
    left: 0;
    top: 0;
    fill: none;
    stroke: #fff;
    stroke-dasharray: 150 480;
    stroke-dashoffset: 150;
    transition: 1s ease-in-out;
  }
  
  .btn2:hover {
    transition: 1s ease-in-out;
    background: #5e5e5e;
  }
  
  .btn2:hover svg {
    stroke-dashoffset: -480;
  }
  
  .btn2 span {
    color: white;
    font-size: 18px;
    font-weight: 100;
  }

  .container1 {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 38vh;
    margin: auto;
    
  }

  a {
    font-size: 2.8vh;
    padding-top: 30px;
    padding-bottom: 0px;
    padding-left: 10px;
    padding-right: 10px;
    color: #000000;
    text-transform: uppercase;
    font-family: Portfolio;
   
    text-align: center;
    
    
  }

  .btn {
    text-decoration: none;
    border: 2px solid rgb(70, 70, 70);
    position: relative;
    overflow: hidden;
    top: 0px;
    transition: all .3s ease;
    border-radius: 4px;
  }
  
  .btn:hover {
    box-shadow: 1px 1px 25px 10px rgba(0, 204, 255, 0.5);
    color: #ffffff;
  }
  
  .btn:before {
    content: "";
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(
      120deg,
      transparent,
      rgba(0, 204, 255, .5),
      transparent
    );
    transition: all .3s;
  }
  
  .btn:hover:before {
    left: 100%;
  }

  .button8 {
    position: relative;
    transition: all .3s ease;
    color: rgb(44, 44, 44);
    border: 2px solid rgb(70, 70, 70);
    font-family: Portfolio;
    text-transform: uppercase;
    text-align: center;
    line-height: 1;
    font-size: 2.8vh;
    background-color : transparent;
   
    outline: none;
    border-radius: 4px;
    top: 39vh;
    justify-content: center;
    left: 14vh;
    width: 22vh;
    padding-top: 15px;
    padding-bottom: 15px;

}
.button8:hover {
    color: #ffffff;
    background-color: rgb(82, 82, 82);
    box-shadow: 1px 1px 25px 10px rgba(0, 204, 255, 0.5);
}

 
  

 


