.eduBox{
    position: relative;
    width: 55vh;
    height: 60vh;
    background-color: rgb(255, 255, 255);
    top: 45vh;
    left: 10vh;
    border-radius: 10px;
    box-shadow: 0px 0px 20px rgb(0, 0, 0);
}

.edulab{
    position: relative;
    color: grey;
    font-family: Portfolio;
    font-size: 5vh;
    margin: auto;
    display: flex;
    justify-content: center;
    left: -5px;
    top: 2vh;
}


.wol{
    position: absolute;
    width: auto;
    top: 11vh;
    left: 7vh;
    margin: auto;
    font-family: Marvin-pro;
    font-weight: normal;
    font-size: 3vh;
    color: rgb(170, 170, 170);
}


.wol::before {
    content: '';
    position: absolute;
    bottom: 8px;
    left: -12px;
    width: 4px;
    height: 4px;
    background-color: rgb(46, 46, 46);
    border-radius: 50%;
    }

    .wolid{
        position: absolute;
        width: 370px;
        top: 16.5vh;
        left: 3vh;
        font-family: Marvin-pro;
        font-weight: normal;
        font-size: 20px;
        color: rgb(138, 138, 138);
        padding-left: 40px;
        padding-right: 40px;
    }

    .Ctu{
        position: absolute;
        width: auto;
        top: 26vh;
        left: 7vh;
        margin: auto;
        font-family: Marvin-pro;
        font-weight: normal;
        font-size: 3vh;
        color: rgb(170, 170, 170);
        
    }

    .Ctu::before {
        content: '';
        position: absolute;
        bottom: 8px;
        left: -12px;
        width: 4px;
        height: 4px;
        background-color: rgb(27, 27, 27);
        border-radius: 50%;
        }

        .Ctuid{
            position: absolute;
            width: 370px;
            top: 32vh;
            left: 3vh;
            font-family: Marvin-pro;
            font-weight: normal;
            font-size: 20px;
            color: rgb(138, 138, 138);
            padding-left: 40px;
            padding-right: 40px;
        }


        


        .button6{
            position: absolute;
            width: 20vh;
            height: 3vh;
            background-color: #0093E9;
            background-image: linear-gradient(160deg, #0093E9 0%, #80D0C7 100%);
            border-radius: 10px;
            top: 49vh;
            left: 25vh;
            cursor: pointer;

        }

        .button6:hover{
            transform: scale(1.1);
            box-shadow: 0px 0px 20px rgb(8, 185, 255);
            transition: 0.2s;
            color: black;
        }

        .de{
            position: absolute;
            color: black;
            font-family: Portfolio;
            font-size: 2vh;
            top: 2vh;
            left: 3vh;
        }






