*{
  margin: 0;
  padding: 0;
  font-family:Zander;
  font-size: 7vh;
}

.main{
  width: 100%;
  height: 100vh;
}

video{
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.content{
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: rgb(255, 255, 255);
  font-size: 25px;
}

.p11{
  position: relative;
  font-family: Portfolio;
  font-size: 4vh;
}

.overlay{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,.6);
}


@media only screen and (max-width: 600px) {
  .body{
    margin: auto;
  }
}





