.AboutBox{
    position: absolute;
    width: 80vh;
    height: 50vh;
    float: right;
    right: 30%;
    top: 168vh;
    box-shadow: 0px 0px 15px rgb(82, 82, 82);
    background-color: snow;
    margin: auto;
  }

  .About1{
    position: relative;
    text-align: center;
    color: gray;
    margin: auto;
    font-family: Portfolio;
    font-weight: lighter;
    font-size: 6vh;
}

.p{
  position: relative;
  width: auto;
  top: 10px;
  left: 0px;
  font-family: Marvin-pro;
  font-weight: normal;
  font-size: 30%;
  color: rgb(138, 138, 138);
  padding-left: 40px;
  padding-right: 40px;
  margin: inherit;
  padding-bottom: 40px;
}



/* -----------------------------------------------------------------Mobile--------------------------------------------------------------- */




@media only screen and (max-width: 600px) {
  .AboutBox{
    position: absolute;
    width: 89%;
    height: 80vh;
    float: right;
    top: 271vh;
    box-shadow: 0px 0px 15px rgb(82, 82, 82);
    background-color: snow;
    margin: auto;
    left: 2.5vh;
    border-radius: 10px;
  }

  .About1{
    position: relative;
    text-align: center;
    color: gray;
    margin: auto;
    font-family: Portfolio;
    font-weight: lighter;
    font-size: 6vh;
}

.p{
  position: relative;
  width: auto;
  top: 10px;
  left: 0px;
  font-family: Marvin-pro;
  font-weight: normal;
  font-size: 30%;
  color: rgb(138, 138, 138);
  padding-left: 40px;
  padding-right: 40px;
  margin: inherit;
  padding-bottom: 40px;
}
}