.social{
    position: sticky;
    top: -1px;
}

.App{
    height: 4400px;
    background-color: rgb(235, 233, 233);
}

.Skills{
    position: relative;
    background-image: url('./assets/Images/snow.gif');
    height: 1000px;
    top: 30vh;
    box-shadow: 0px 0px 20px rgb(0, 0, 0);
}


  @media only screen and (max-width: 600px) {
    .App{
        height: 8400px;
        background-color: rgb(235, 233, 233);
    }


    .body{
      margin: auto;
    }

    .Skills{
        position: relative;
        background-image: url('./assets/Images/snow.gif');
        height: 2700px;
        top: 230vh;
        box-shadow: 0px 0px 20px rgb(0, 0, 0);
    }
  }

  @media only screen and (max-width: 400px) {
    .App{
        height: 7800px;
        background-color: rgb(235, 233, 233);
    }
    .Skills{
        position: relative;
        background-image: url('./assets/Images/snow.gif');
        height: 2500px;
        top: 230vh;
        box-shadow: 0px 0px 20px rgb(0, 0, 0);
    }
  }









  