.social{
    position: sticky, absolute;
    width: 100%;
    height: 6vh;
    background-color: rgb(4, 6, 17);
    border-radius: 5px;
    overflow: hidden;
    display: block;
    z-index: 9999;
}

.softlab{
    position: absolute;
    top: 1.2vh;
    left: 3vh;
    color: snow;
    font-size: 3vh;
    font-family: Portfolio;
}

.faceimg{
    position: absolute;
    top: 5px;
    right: 23vh;
    width: 5vh;
    height: 5vh;
    border-radius: none;
    border: none;
    transition: transform .2s;
}

.faceimg:hover {
    transform: scale(1.4);
}

